import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Axios from 'axios';

import Todos from './components/Todos';
import Header from './components/layouts/Header';
import About from './components/pages/About';
import TodoLink from './components/TodoLink';

import uuid from 'uuid';
import './App.css';

class App extends Component {
  state = {
    todos: []
  }
  // componentDidMount() {
  //   Axios.get('https://jsonplaceholder.typicode.com/todos?_direction=desc')
  //     .then(res => this.setState({ todos: res.data }))
  //     .catch(err => console.log(err));
  // }

  /** Add new todo */
  addTodo = (title) => {
    /** add todo my way (to save to local) */
    const newTodo = {
      id: uuid.v4(),
      title,
      completed: false,
    }
    this.setState({ todos: [...this.state.todos, newTodo] });

    /** Add todo with json placeholder */
    // Axios.post('https://jsonplaceholder.typicode.com/todos', {
    //   title,
    //   completed: false,
    // })
    //   .then(res => this.setState({ todos: [...this.state.todos, res.data] }));

  }

  /** Check complete */
  markComplete = (id) => {
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      })
    });
  }

  /** Delete todo */
  deleteTodo = (id) => {
    this.setState({
      todos: this.state.todos.filter(todo => todo.id !== id)
    });
  }

  render() {
    return (
      <Router>
        <div className="App">
          <TodoLink />
          <Route exact path={`${this.props.match.url}/`} render={props => (
            <React.Fragment>
              <Header addTodo={this.addTodo} />
              <div style={{ width: '50%', boxShadow: '2px 2px 15px rgba(0,0,0,0.24)', margin: '6em auto', padding: '4em 1em 1em 1em', background: 'rgba(242, 17, 81,0.14)', borderRadius: '10px' }}>
                <Todos todos={this.state.todos} markComplete={this.markComplete} deleteTodo={this.deleteTodo} />
              </div>
            </React.Fragment>
          )} />
          <Route path={`${this.props.match.url}/about`} component={About} />
        </div >
      </Router>
    )
  }
}

export default App;
