import React,
{ Component } from 'react'

export class AddTodo extends Component {
    state = {
        title: '',
    }

    saveTitle = (e) => this.setState({ [e.target.name]: e.target.value });

    onSubmit = (e) => {
        e.preventDefault();
        this.props.addTodo(this.state.title);
        this.setState({ title: '' });
    }

    render() {
        return (
            <form onSubmit={this.onSubmit} style={{ display: 'flex', position: 'relative', top: '1em' }}>
                <input
                    type="text"
                    name="title"
                    placeholder="Add new todo..."
                    style={inputStyle}
                    value={this.state.title}
                    onChange={this.saveTitle} />
                <input type="submit" value="Add" className="btn" style={{ flex: 1, ...submitStyle }} />
            </form>
        )
    }
}

const submitStyle = {
    borderRadius: '0.3em',
    background: '#fff',
    border: 'none',
    boxShadow: '2px 2px 2px rgba(0,0,0,0.20)',
}

const inputStyle = {
    flex: 10,
    background: '#fff',
    padding: '0.5em',
    border: 'none',
    margin: '0 5px',
    boxShadow: '2px 2px 2px rgba(0,0,0,0.20)',
    borderRadius: '0.3em',
}

export default AddTodo
