import React, { Component } from 'react'
import { Link } from 'react-router-dom';

class TodoLink extends Component {
    render() {
        return (
            <ul style={todoLinkContainer}>
                <li style={linkContainerStyle}>
                    <Link style={linkStyle} to="/" >Home</Link>
                </li>
                <li style={linkContainerStyle}>
                    <Link style={linkStyle} to="/about" >About</Link>
                </li>
            </ul>
        )
    }
}

const todoLinkContainer = {
    listStyleType: 'none',
    position: 'absolute',
    left: '10%',
    top: '20%',
}

const linkContainerStyle = {
    marginBottom: '60px',
}

const linkStyle = {
    fontSize: '14px',
    background: '#fff',
    boxShadow: '3px 4px 5px rgba(0,0,0,0.24)',
    borderRadius: '50%',
    padding: '22px 12px',
}


export default TodoLink
